.menu {
  max-width: 1280px;
  min-height: 116px;

  display: flex;
  align-items: center;

  position: sticky;
  top: 0;

  z-index: 1;
  margin-top: -2px;

  @include iPad {
    max-width: $ipad-width;
    min-height: 80px;
  }

  &__dropdown-wrap {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;

    @include mobile {
      margin: 0 auto;
    }
  }

  &__content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
    width: 1280px;

    @include iPad {
      width: 768px;
      min-height: 80px;
    }

    @include mobile {
      width: 380px;
      min-height: 80px;
      padding: 0 20px;
    }
  }

  &__logo-img {
    width: 100px;
    height: 42px;

    @include mobile {
      width: 80px;
      height: 34px;
    }
  }

  &__settings-wrap {
    @include iPad {
      display: none;
    }
  }

  &__menu-mobile {
    display: block;
    cursor: pointer;

    @include Desktop {
      display: none;
    }

    & img {
      @include mobile {
        width: 32px;
      }
    }
  }
}
